import React, { useEffect, useState } from 'react'
import { useNavigate } from 'react-router-dom';
import jwt_decode from 'jwt-decode'
import axios from 'axios'
import  NavBar  from './NavBar'
import './styles/style.css'



function profile() {
  axios.defaults.headers.common = { 'Authorization': sessionStorage.getItem('Token') }
  const navigate = useNavigate()
  const [user, setUser] = useState('')
  const [admin, setAdmin] = useState('')
  useEffect(() => {
    try {
      var token = sessionStorage.getItem('Token')
      token = token.replace('Bearer', '');
      const decoded = jwt_decode(token);
      axios.get(`http://localhost:5000/user/profile/${decoded.id}`)
        .then(res => {
          setUser(res.data)
          setAdmin(res.data.admin)
        })
        .catch(err => {
          console.log(err)
        })
       
    }
    catch (error) {
      navigate('/')
    }
    

  }, [])

  
 

  return (
    <>
      <div>

      <NavBar props={[ admin , 'home']} />
      </div>
      <div>
      <div id="carouselExampleControls" class="carousel slide" data-bs-ride="carousel">
  <div className="carousel-inner p-1 ">
    <div className="carousel-item active">
      <img src='images/welcome.jpg' className="d-block" alt=".." />
    </div>
    <div className="carousel-item">
    <div className='outerDiv .d-h-60vh ' >
    <div className="card mb-3 bg-dark">
  <img src="images/user-512.png" class="card-img-top p-3" alt=".."/>
  <div className="card-body text-light">
    <h5 className="card-title">Profile</h5>
    <p className="card-text">
     
        <p><span >First Name : </span>{user.fname}
        </p>
        <p><span>Last Name : </span>{user.lname}
        </p>
        <p><span>Email : </span>{user.email}
        </p>
        <p><span>password : </span>{user.password}
        </p>
      </p>
    <p className="card-text"><small className="text-success">Logged in</small></p>
  </div>
</div>
</div>  
    </div>
  </div>
  <button className="carousel-control-prev" type="button" data-bs-target="#carouselExampleControls" data-bs-slide="prev">
    <span className="carousel-control-prev-icon" aria-hidden="true"></span>
    <span className="visually-hidden">Previous</span>
  </button>
  <button className="carousel-control-next" type="button" data-bs-target="#carouselExampleControls" data-bs-slide="next">
    <span className="carousel-control-next-icon" aria-hidden="true"></span>
    <span className="visually-hidden">Next</span>
  </button>
</div>
</div>
    </>
  )
}

export default profile